:root {
  --accent: #009026;
  --accent-2: #EDFFEB;
  --heading: #27272E;
  --text-1: #425466;
  --text-2: #7A7A9D;
  --background-primary: #F7FAFC;
  --background-secondary: #ffffff;
  --borders: #EDF2F7;
  --active: #68DBF2;
  --complete: #2ECF88;
  --awaiting: #F7936F;
  --drafts: #FAB592;
  --invoice: #FE6F7E;
  --quotes: #4CAFD0;
}

body.menu-closed .top-bar {
  max-width: calc(100vw - 90px);
}
body .top-bar {
  width: 100vw;
  max-width: calc(100vw - 250px);
  margin: 0 0 0 auto;
  transition: max-width 0.3s ease-in-out;
}
body .top-bar .inside-top-bar {
  margin: 0;
  height: 80px;
  display: flex;
  align-items: center;
  max-width: 100%;
  padding: 0 46px 0 20px;
  flex-wrap: nowrap;
}
body .top-bar .inside-top-bar .widget:nth-child(2n+1) {
  order: unset;
}
body .top-bar .inside-top-bar .AjaxSearchLiteWidget {
  margin: 0 auto 0 12%;
}
body .top-bar .inside-top-bar .page-title p {
  display: none;
}
body .top-bar .inside-top-bar .page-title h1 {
  font-size: 1.75rem;
  line-height: 39px;
  margin-bottom: 0px;
}
body .top-bar .inside-top-bar .user-account-avatar-outer > .wp-block-group__inner-container > p {
  display: none;
}
body .top-bar .inside-top-bar .user-account-avatar-outer #user-account-avatar p {
  border-radius: 50%;
  margin-bottom: 0;
  line-height: 0;
  overflow: hidden;
}

#masthead {
  height: 100vh;
  max-width: 250px;
  flex-wrap: wrap;
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 20px;
  overflow: hidden;
  border: 1px solid var(--borders);
  transition: max-width 0.3s ease-in-out;
}
#masthead #burger-menu svg {
  cursor: pointer;
  margin-bottom: 14px;
}
#masthead .inside-header {
  flex-wrap: wrap;
  padding: 0 25px;
}
#masthead .inside-header #site-navigation {
  margin-left: 0;
  margin-bottom: 30px;
  width: 100%;
  border-bottom: 1px solid var(--borders);
  padding-bottom: 30px;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu {
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: flex-start;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a {
  padding: 0;
  display: flex;
  align-items: center;
  line-height: 34px;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a span {
  opacity: 1;
  transition: opacity 0.2s ease-in-out, font-size 0.2s ease-in-out;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a svg {
  margin-right: 20px;
  transition: margin-right 0.3s ease-in-out;
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu a:hover svg path {
  fill: var(--accent);
}
#masthead .inside-header #site-navigation #primary-menu #menu-main-menu .current-menu-item a svg path {
  fill: var(--accent);
}
#masthead .inside-header .site-branding-container {
  flex-wrap: wrap;
  margin-bottom: 24px;
}
#masthead .inside-header .site-branding-container .site-branding a {
  opacity: 1;
  transition: all 0.2s ease-in-out;
}
#masthead .inside-header .site-branding-container #burger-menu {
  width: 100%;
}

body.menu-closed #masthead {
  max-width: 90px;
}
body.menu-closed #masthead .inside-header #primary-menu #menu-main-menu a span {
  opacity: 0 !important;
  font-size: 0rem;
  transition: opacity 0.2s ease-in-out, font-size 0.3s ease-in-out 0.2s;
}
body.menu-closed #masthead .inside-header #primary-menu #menu-main-menu a svg {
  margin-right: 0px !important;
}
body.menu-closed #masthead .inside-header .site-branding-container .site-branding a {
  opacity: 0;
  font-size: 0;
}

.checklist-outer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.checklist-outer .title-and-count {
  display: flex;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.checklist-outer .title-and-count p {
  transition: font-size 0.3s ease-in-out;
  width: 70%;
}
.checklist-outer .title-and-count .fraction {
  width: auto;
  background-color: var(--accent-2);
  border-radius: 2em;
  height: 20px;
  max-width: 39px;
  text-align: center;
  font-size: 0.75rem;
  padding: 0px 10px;
  display: flex;
  align-items: center;
}
.checklist-outer p {
  width: 100%;
  text-align: center;
}

#account-checklist {
  display: flex;
  flex-direction: column;
  order: 4;
  overflow: hidden;
  max-height: 800px;
  transition: opacity 0.3s ease-in-out, max-height 0.3s ease-in-out, font-size 0.3s ease-in-out;
}
#account-checklist label {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
}
#account-checklist label input {
  pointer-events: none;
}

.checklist-outer .checklist-progress-bar {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  width: 100%;
  height: 12px;
  max-width: 250px;
  border-radius: 2em;
  overflow: hidden;
  background-color: #EDF2F7;
}
.checklist-outer .checklist-progress-bar span.active {
  background-color: var(--accent);
}
.checklist-outer .checklist-progress-bar span.active.final {
  border-top-right-radius: 2em;
  border-bottom-right-radius: 2em;
}

.menu-closed .title-and-count p {
  font-size: 0 !important;
}
.menu-closed #account-checklist {
  display: none;
}
.menu-closed .checklist-outer .percentage {
  display: none;
}
.menu-closed .checklist-outer .checklist-progress-bar {
  grid-template-columns: repeat(1, 1fr);
  max-width: 12px;
  height: 100px;
  border-radius: 2em;
  display: none;
}
.menu-closed .checklist-outer .checklist-progress-bar .active:not(.final) {
  border-radius: 0;
}
.menu-closed .checklist-outer .checklist-progress-bar .active.final {
  border-radius: 0 0 2em 2em;
}

html body {
  display: flex;
  flex-wrap: wrap;
}
html body.admin-bar #masthead {
  top: 32px;
}
html body.menu-closed #page, html body.menu-closed .site-footer {
  max-width: calc(100vw - 90px);
}
html body.menu-closed .site-footer {
  order: 4;
  width: 100vw;
}
html body #page {
  order: 3;
  max-width: calc(100vw - 250px);
  margin: 0 0 0 auto;
  width: 100vw;
  transition: max-width 0.3s ease-in-out;
}
html body .site-footer {
  order: 4;
  max-width: calc(100vw - 250px);
  width: 100vw;
  margin-left: auto;
  transition: max-width 0.3s ease-in-out;
}